import React from 'react'
import { Link } from 'react-scroll'
    export const Header = () => {
    //     document.addEventListener("DOMContentLoaded", function(){
    // window.addEventListener('scroll', function() {
    //   if (window.scrollY > 50) {
    //     document.getElementById('navbar_top').classList.add('fixed-top');
    //     // add padding top to show content behind navbar
    //     let navbar_height = document.querySelector('.navbar').offsetHeight;
    //     document.body.style.paddingTop = navbar_height + 'px';
    //   } else {
    //     document.getElementById('navbar_top').classList.remove('fixed-top');
    //      // remove padding top from body
    //     document.body.style.paddingTop = '0';
    //   } 
    // });
    // });
    window.onscroll = function () { 
        // var link = document.getElementsByTagName("nav")[0].getElementsByTagName("a");
        // console.log(link)
        var link=document.getElementsByTagName("nav")[0].getElementsByTagName('a')
        if (document.body.scrollTop >= 200 || document.documentElement.scrollTop >= 200 ) {
            document.getElementById('navbar_top').classList.add("scrolled");
            Array.from(link).forEach(element => {
                element.classList.add("ntext1")
            });
        } 
        else {
            document.getElementById('navbar_top').classList.remove("scrolled");
            Array.from(link).forEach(element => {
                element.classList.remove("ntext1")
            });
        }
    };
    return (
        <div>
            <div id="Home" className="bg">
            <br/>
            <center><h2 style={{height:"100%",marginTop:"250px",marginBottom:"250px",fontFamily: "Hind,Arial,Sans-Serif", fontSize: "46px",fontWeight: "700", color:"white"}}>Vikrant <br/>Software Engineer</h2></center>
</div>
<nav id="navbar_top" className="navbar navbar-expand-lg fixed-top navbar-dark transparent">
                 <div id ="div" className="container-fluid">
                 <button style={{backgroundColor:"black"}}className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                     <div className="collapse navbar-collapse" id="navbarSupportedContent" >
                         <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                             <li className="nav-item ">
                             <Link  className="nav-link active navlink" to="Home">Home</Link>
                             </li>
                             <li className="nav-item">
                                 <Link className="nav-link active navlink" aria-current="page" to="about">About Me</Link>
                             </li>
                             <li className="nav-item">
                                 <Link className="nav-link active navlink" to="skills">Skills</Link>
                             </li>
                             <li className="nav-item ">
                                 <Link className="nav-link active navlink" to="contactme" tabIndex="-1" aria-disabled="true">Contact Me</Link>
                             </li>
                         </ul>
                     </div>
                 </div>
             </nav>
        </div>
    )
}





